"use client"

import { useState } from "react"
import { ShoppingCart, Search, Menu, X, MapPin, Phone, User, LogOut, Heart } from "lucide-react"
import { useCart } from "@/hooks/use-cart"
import { useUser } from "@/contexts/user-context"
import Link from "next/link"

interface HeaderProps {
  cartOpen: boolean
  onCartToggle: (open: boolean) => void
}

export default function Header({ cartOpen, onCartToggle }: HeaderProps) {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)
  const [searchQuery, setSearchQuery] = useState("")
  const [userMenuOpen, setUserMenuOpen] = useState(false)
  const { items } = useCart()
  const { user, logout } = useUser()
  const cartCount = items.reduce((total, item) => total + item.quantity, 0)

  return (
    <header className="sticky top-0 z-50 bg-white border-b border-border shadow-sm">
      {/* Top info bar */}
      <div className="bg-secondary text-secondary-foreground text-sm py-2 px-4">
        <div className="max-w-6xl mx-auto flex items-center justify-between">
          <div className="flex items-center gap-4">
            <div className="flex items-center gap-1">
              <MapPin size={14} />
              <span>Free shipping on orders above â‚¹999</span>
            </div>
          </div>
          <div className="flex items-center gap-2">
            <Phone size={14} />
            <span>Support: 1-800-ARTISAN</span>
          </div>
        </div>
      </div>

      {/* Main header */}
      <div className="max-w-6xl mx-auto px-4 py-3">
        <div className="flex items-center justify-between gap-4">
          {/* Logo */}
          <Link href="/" className="flex-shrink-0 hover:opacity-80 transition-opacity">
            <h1 className="text-2xl font-bold text-primary">ranaartcart</h1>
            <p className="text-xs text-muted-foreground">Handmade Arts Marketplace</p>
          </Link>

          {/* Search bar - hidden on mobile */}
          <div className="hidden md:flex flex-1 mx-6">
            <div className="relative w-full">
              <input
                type="text"
                placeholder="Search for crafts, art, handmade items..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="w-full px-4 py-2 bg-input text-foreground placeholder-muted-foreground rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
              />
              <Search className="absolute right-3 top-1/2 -translate-y-1/2 text-muted-foreground" size={18} />
            </div>
          </div>

          {/* Actions */}
          <div className="flex items-center gap-2">
            {/* Seller link */}
            <Link
              href="/seller/login"
              className="hidden md:block px-3 py-2 text-sm font-semibold text-primary hover:bg-muted rounded-lg transition-colors"
            >
              Sell
            </Link>

            {/* User account button */}
            <div className="relative hidden md:block">
              <button
                onClick={() => setUserMenuOpen(!userMenuOpen)}
                className="p-2 hover:bg-muted rounded-lg transition-colors"
                aria-label="User account"
              >
                <User size={24} className="text-primary" />
              </button>
              {userMenuOpen && (
                <div className="absolute right-0 mt-2 w-48 bg-card rounded-lg shadow-lg border border-border overflow-hidden">
                  {user ? (
                    <>
                      <div className="px-4 py-3 border-b border-border">
                        <p className="font-semibold text-foreground">{user.name}</p>
                        <p className="text-xs text-muted-foreground">{user.email}</p>
                      </div>
                      <Link
                        href="/dashboard"
                        className="block px-4 py-2 hover:bg-muted text-foreground transition-colors"
                      >
                        My Dashboard
                      </Link>
                      <Link href="/orders" className="block px-4 py-2 hover:bg-muted text-foreground transition-colors">
                        My Orders
                      </Link>
                      <Link
                        href="/wishlist"
                        className="block px-4 py-2 hover:bg-muted text-foreground transition-colors"
                      >
                        Wishlist
                      </Link>
                      <button
                        onClick={() => {
                          logout()
                          setUserMenuOpen(false)
                        }}
                        className="w-full text-left px-4 py-2 hover:bg-muted text-foreground transition-colors flex items-center gap-2 border-t border-border"
                      >
                        <LogOut size={16} />
                        Logout
                      </button>
                    </>
                  ) : (
                    <>
                      <Link href="/login" className="block px-4 py-2 hover:bg-muted text-foreground transition-colors">
                        Login
                      </Link>
                      <Link href="/signup" className="block px-4 py-2 hover:bg-muted text-foreground transition-colors">
                        Sign Up
                      </Link>
                    </>
                  )}
                </div>
              )}
            </div>

            {/* Wishlist Link */}
            <Link
              href="/wishlist"
              className="p-2 hover:bg-muted rounded-lg transition-colors hidden md:block"
              aria-label="Wishlist"
            >
              <Heart size={24} className="text-primary" />
            </Link>

            <button
              onClick={() => onCartToggle(!cartOpen)}
              className="relative p-2 hover:bg-muted rounded-lg transition-colors"
              aria-label="Shopping cart"
            >
              <ShoppingCart size={24} className="text-primary" />
              {cartCount > 0 && (
                <span className="absolute -top-1 -right-1 bg-accent text-accent-foreground text-xs font-bold w-5 h-5 rounded-full flex items-center justify-center">
                  {cartCount}
                </span>
              )}
            </button>

            <button
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
              className="md:hidden p-2 hover:bg-muted rounded-lg transition-colors"
              aria-label="Menu"
            >
              {mobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
            </button>
          </div>
        </div>

        {/* Mobile search */}
        <div className="md:hidden mt-3">
          <div className="relative w-full">
            <input
              type="text"
              placeholder="Search..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full px-4 py-2 bg-input text-foreground placeholder-muted-foreground rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
            />
            <Search className="absolute right-3 top-1/2 -translate-y-1/2 text-muted-foreground" size={18} />
          </div>
        </div>

        {/* Mobile menu */}
        {mobileMenuOpen && (
          <nav className="md:hidden mt-4 pb-4 space-y-2">
            <Link href="/seller/login" className="block px-4 py-2 hover:bg-muted rounded-lg font-semibold">
              Become a Seller
            </Link>
            <Link href="/orders" className="block px-4 py-2 hover:bg-muted rounded-lg">
              My Orders
            </Link>
            <Link href="/wishlist" className="block px-4 py-2 hover:bg-muted rounded-lg">
              Wishlist
            </Link>
            <Link href="/dashboard" className="block px-4 py-2 hover:bg-muted rounded-lg">
              Account
            </Link>
          </nav>
        )}
      </div>
    </header>
  )
}
