"use client"

import type React from "react"
import { createContext, useContext, useState, useEffect } from "react"

export interface Order {
  id: string
  orderNumber: string
  date: string
  items: {
    id: string
    name: string
    price: number
    quantity: number
    image: string
  }[]
  total: number
  status: "pending" | "processing" | "shipped" | "delivered" | "cancelled"
  trackingNumber?: string
  estimatedDelivery?: string
  customerInfo: {
    name: string
    email: string
    phone: string
    address: string
    city: string
    pincode: string
  }
}

export interface User {
  id: string
  name: string
  email: string
  phone: string
  addresses: {
    id: string
    name: string
    email: string
    phone: string
    address: string
    city: string
    pincode: string
    isDefault: boolean
  }[]
  wishlist: string[]
}

interface UserContextType {
  user: User | null
  isLoggedIn: boolean
  orders: Order[]
  login: (email: string, password: string) => Promise<void>
  signup: (name: string, email: string, password: string, phone: string) => Promise<void>
  logout: () => Promise<void>
  addOrder: (order: Order) => void
  updateUserInfo: (updates: Partial<User>) => void
  addAddress: (address: Omit<User["addresses"][0], "id">) => void
  addToWishlist: (productId: string) => void
  removeFromWishlist: (productId: string) => void
}

const UserContext = createContext<UserContextType | undefined>(undefined)

export function UserProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [orders, setOrders] = useState<Order[]>([])
  const [mounted, setMounted] = useState(false)

  // Load user/token and orders from localStorage on mount, then verify token via /api/auth/me
  useEffect(() => {
    const savedUser = localStorage.getItem("ranaartcart_user")
    const savedOrders = localStorage.getItem("ranaartcart_orders")
    const accessToken = localStorage.getItem("accessToken")

    if (savedUser) {
      try {
        setUser(JSON.parse(savedUser))
      } catch (e) {
        console.error("Failed to load user:", e)
      }
    }

    if (savedOrders) {
      try {
        setOrders(JSON.parse(savedOrders))
      } catch (e) {
        console.error("Failed to load orders:", e)
      }
    }

    const verify = async () => {
      if (accessToken) {
        try {
          await fetchMe(accessToken)
        } catch (e) {
          console.warn("Stored token invalid, clearing", e)
          localStorage.removeItem("accessToken")
          localStorage.removeItem("ranaartcart_user")
          setUser(null)
        }
      }
    setMounted(true)
    }

    void verify()
  }, [])

  // Save user to localStorage when it changes
  useEffect(() => {
    if (mounted && user) {
      localStorage.setItem("ranaartcart_user", JSON.stringify(user))
    }
  }, [user, mounted])

  // Save orders to localStorage when they change
  useEffect(() => {
    if (mounted) {
      localStorage.setItem("ranaartcart_orders", JSON.stringify(orders))
    }
  }, [orders, mounted])

  const fetchMe = async (accessToken: string) => {
    const res = await fetch("/api/auth/me", {
      headers: { Authorization: `Bearer ${accessToken}` },
    })
    const data = await res.json()
    if (!res.ok) {
      throw new Error(data.error || "Unable to load user")
    }
    const newUser: User = {
      id: data.user.id,
      name: data.user.name,
      email: data.user.email,
      phone: "",
      addresses: [],
      wishlist: [],
    }
    setUser(newUser)
    return newUser
  }

  const login = async (email: string, password: string) => {
    const res = await fetch("/api/auth/login", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email, password }),
    })
    const data = await res.json()
    if (!res.ok) {
      throw new Error(data.error || "Login failed")
    }
    localStorage.setItem("accessToken", data.accessToken)
    await fetchMe(data.accessToken)
  }

  const signup = async (name: string, email: string, password: string, phone: string) => {
    const res = await fetch("/api/auth/signup", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ name, email, password, phone }),
    })
    const data = await res.json()
    if (!res.ok) {
      throw new Error(data.error || "Signup failed")
    }
    // Immediately log in after successful signup
    await login(email, password)
  }

  const logout = async () => {
    try {
      await fetch("/api/auth/logout", { method: "POST" })
    } catch (e) {
      console.error("Logout request failed", e)
    }
    setUser(null)
    localStorage.removeItem("ranaartcart_user")
    localStorage.removeItem("accessToken")
  }

  const addOrder = (order: Order) => {
    setOrders((prev) => [...prev, order])
  }

  const updateUserInfo = (updates: Partial<User>) => {
    setUser((prev) => (prev ? { ...prev, ...updates } : null))
  }

  const addAddress = (address: Omit<User["addresses"][0], "id">) => {
    setUser((prev) =>
      prev
        ? {
            ...prev,
            addresses: [
              ...prev.addresses,
              {
                id: `addr_${Date.now()}`,
                ...address,
              },
            ],
          }
        : null,
    )
  }

  const addToWishlist = (productId: string) => {
    setUser((prev) =>
      prev && !prev.wishlist.includes(productId)
        ? {
            ...prev,
            wishlist: [...prev.wishlist, productId],
          }
        : prev,
    )
  }

  const removeFromWishlist = (productId: string) => {
    setUser((prev) =>
      prev
        ? {
            ...prev,
            wishlist: prev.wishlist.filter((id) => id !== productId),
          }
        : prev,
    )
  }

  return (
    <UserContext.Provider
      value={{
        user,
        isLoggedIn: !!user,
        orders,
        login,
        signup,
        logout,
        addOrder,
        updateUserInfo,
        addAddress,
        addToWishlist,
        removeFromWishlist,
      }}
    >
      {children}
    </UserContext.Provider>
  )
}

export function useUser() {
  const context = useContext(UserContext)
  if (!context) {
    throw new Error("useUser must be used within UserProvider")
  }
  return context
}
